<template>
    <div id="login" style="padding-top: 200px;padding-right: 550px;padding-left: 550px;opacity: 0.8;">
        <loadings></loadings>
        <el-card id="login-card" class="box-card">
            <form>
                <div style="margin-top: 25px;margin-bottom: 25px">
                    <el-input placeholder="请输入内容" v-model="inputs.account" @keyup.enter.native="loginsubmit">
                        <template slot="prepend">用户名</template>
                    </el-input>
                </div>
                <div style="margin-top: 30px;margin-bottom: 25px">
                    <el-input type="password" placeholder="请输入内容" v-model="inputs.password"
                              v-on:keyup.enter.native="loginsubmit">
                        <template slot="prepend"><span style="white-space: pre">密码{{"\t"}}{{" "}}{{" "}}</span>
                        </template>
                    </el-input>
                </div>
                <span style="white-space: pre;color: red;font-weight: bold">{{login_result.login_info}}</span>
                <div style="text-align: center;margin-top: 15px;margin-bottom: 10px">
                    <el-button @click="loginsubmit" type="primary">登录</el-button>
                </div>
            </form>
        </el-card>
    </div>
</template>

<script>
    /* eslint no-irregular-whitespace: "off"*/
    import '@/assets/js/include.js'
    import fun from '@/assets/js/OJFunction'
    import {hex_md5} from '@/assets/js/md5.js'
    import Loadings from '@/components/loading/loading'

    export default {
        name: "login",
        components: {
            loadings: Loadings
        },
        data() {
            return {
                page_setting: {
                    access_token: "",
                    refresh_token: "",
                    language: ""
                },
                inputs: {
                    account: "",
                    password: ""
                },
                login_result: {
                    has_error: false,
                    login_info: "　",
                }
            }
        },
        methods: {
            loginsubmit: function () {
                this.login_result.has_error = false;
                this.login_result.login_info = "　";
                this.inputs.password = hex_md5(this.inputs.password);
                this.$http.post(this.GLOBAL.OJ_DEFAULT_BACK_SERVE + '/admin/users/login',
                    {
                        account: this.inputs.account,
                        password: this.inputs.password,
                    }, {
                        emulateJSON: true,
                        headers: {
                            OJ_ACCESS_TOKEN: this.page_setting.access_token,
                            OJ_REFRESH_TOKEN: this.page_setting.refresh_token
                        }
                    }).then(res => {
                    console.log(res)
                    var error_type = res.body.OJ_ERROR_TYPE;
                    if (error_type != this.GLOBAL.OJ_ERROR_NO_ERROR) {
                        this.login_result.has_error = true;
                        this.inputs.password = "";
                    }
                    if (error_type == this.GLOBAL.OJ_ERROR_IP_BE_BANNED) {
                        this.login_result.login_info = this.$t('WARN.OJ_MESSAGE_IP_BE_BANNED');
                    } else if (error_type == this.GLOBAL.OJ_ERROR_ACCOUNT_BE_BANNED) {
                        this.login_result.login_info = this.$t('WARN.OJ_MESSAGE_ACCOUNT_BE_BANNED');
                    } else if (error_type == this.GLOBAL.OJ_ERROR_NON_EXISTENT_ACCOUNT) {
                        $("#account").focus();
                        this.login_result.login_info = this.$t('WARN.OJ_MESSAGE_NON_EXISTENT_ACCOUNT');
                    } else if (error_type == this.GLOBAL.OJ_ERROR_REPETED_ACCOUNT) {
                        $("#account").focus();
                        this.error_message.account = this.$t('WARN.OJ_MESSAGE_REPETED_ACCOUNT');
                    } else if (error_type == this.GLOBAL.OJ_ERROR_ACCOUNT_BE_BANNED) {
                        this.login_result.login_info = this.$t('WARN.OJ_MESSAGE_ACCOUNT_BE_BANNED');
                    } else if (error_type == this.GLOBAL.OJ_ERROR_WRONG_PASSWORD) {
                        $("#password").focus();
                        this.login_result.login_info = this.$t('WARN.OJ_MESSAGE_WRONG_PASSWORD');
                    } else if (error_type != this.GLOBAL.OJ_ERROR_NO_ERROR) {
                        this.login_result.login_info = "　";
                    }

                    var remember_me = res.body.OJ_USER_INFO.saved;
                    if (this.login_result.has_error == false) {

                        var access_token = res.headers.map['oj_access_token'][0];
                        var refresh_token = res.headers.map['oj_refresh_token'][0];
                        // alert(access_token);
                        if (remember_me) {
                            // alert(1);
                            fun.setCookieWithTime(this.GLOBAL.OJ_DEFAULT_NAME + "access_token", access_token, 7);
                            fun.setCookieWithTime(this.GLOBAL.OJ_DEFAULT_NAME + "refresh_token", refresh_token, 7);
                        } else {
                            fun.setCookie(this.GLOBAL.OJ_DEFAULT_NAME + "access_token", access_token);
                            fun.setCookie(this.GLOBAL.OJ_DEFAULT_NAME + "refresh_token", refresh_token);
                        }
                        window.location.href = "";
                    }
                }, err => {
                    console.log(err.body);
                });
            },
        },
        created() {
            this.page_setting.access_token = fun.getCookie(this.GLOBAL.OJ_DEFAULT_NAME + "access_token");
            this.page_setting.refresh_token = fun.getCookie(this.GLOBAL.OJ_DEFAULT_NAME + "refresh_token");
            var lang = localStorage.getItem(this.GLOBAL.OJ_DEFAULT_NAME + "lang");
            if (lang != null) {
                this.page_setting.language = lang;
            }
            this.$http.post(this.GLOBAL.OJ_DEFAULT_BACK_SERVE + '/admin/home/loginpage',
                {
                    language: this.page_setting.language
                }, {
                    emulateJSON: true,
                    headers: {
                        OJ_ACCESS_TOKEN: this.page_setting.access_token,
                        OJ_REFRESH_TOKEN: this.page_setting.refresh_token
                    }
                }).then(function (res) {
                var error_type = res.body.OJ_ERROR_TYPE;
                if (error_type == this.GLOBAL.OJ_ERROR_HAS_LOGIN) {
                    window.history.go(-1);
                    return;
                }

                lang = res.body.OJ_LANGUAGE_TYPE;
                localStorage.setItem(this.GLOBAL.OJ_DEFAULT_NAME + "lang", lang);
                this.$i18n.locale = lang;

            }, function () {
                console.log('请求失败处理');
            });
        }
    }
</script>

<style scoped lang="scss">
    #login {
        #login-card {
            box-shadow: 0px 0px 35px 0px rgba(255, 255, 255, 0.59);
            -webkit-box-shadow: 0px 0px 35px 0px rgba(255, 255, 255, 0.59);
            -moz-box-shadow: 0px 0px 35px 0px rgba(255, 255, 255, 0.59);
        }
    }

</style>